import CompositionBase from "@/components/CompositionBase";
import grade from '@/assets/js/data';

export default {
  name: 'AccumulateCamps',
  mixins: [CompositionBase],
  created() {
    this.setData();
  },
  methods: {
     setData(){
      this.initData = grade['grade' + JSON.parse(localStorage.getItem('gradeInfo')).id]['2']
    }
  }
}
